import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import MOVIES from './data.json';
import { BrowserRouter } from 'react-router-dom';



class Card extends Component {
  render() {
    return <React.Fragment>
      <li className="list__item">
        <article className="list__item--article item">
          <div className="item--wrap">
            <h2 className="item__title">{this.props.title}</h2>
            <span className="item__year">Year:{this.props.year}</span>
            <span className="item__genres">Genres:{this.props.genres}</span>
            <span className="item__plot">Plot:{this.props.plot}</span>
            <span className="item__director">Director:{this.props.director}</span>
            <span className="item__runtime">Runtime:{this.props.runtime}</span>
            <a href="#">More</a>
          </div>
          <img src={this.props.posterUrl}
               alt="img"
               className="item__img"/>
        </article>
      </li>
    </React.Fragment>
  }
}

class CardList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      displayedMovies: MOVIES
    };
    
    this.handleSearch = this.handleSearch.bind(this);
  }
  
  handleSearch(event) {
    let searchQuery = event.target.value.toLowerCase();
    let displayedMovies = MOVIES.filter(function (el) {
      let searchValue = el.title.toLowerCase();
      return searchValue.indexOf(searchQuery) !== -1;
    });
    
    this.setState({
      displayedMovies: displayedMovies
    })
    
  }
  
  render() {
    return (
      <section className="main">
        <h1>React SPA movie store</h1>
        <input type="text"
        className="main__search"
               onChange={this.handleSearch}
               placeholder="Enter movie name"
        />
        <ul className="main__list list">
          {this.state.displayedMovies.map(function (el) {
            return <Card
              item__title={el.title}
              item__year={el.year}
              item__genres={el.genres}
              item__plot={el.plot}
              item__director={el.director}
              item__runtime={el.runtime}
              item__img={el.posterUrl}
              key={el.id}
            />
          })}
        </ul>
      </section>)
  }
}

ReactDOM.render(<CardList/>, document.getElementById('root'));




